.cart {
    display: flex;
    flex-direction: column;
    min-height: 60vh;
    width: 80vw;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 140px;
    background-color: #ebbbab;
    border-radius: 15px;
    position: relative;
    left: 10%;
}

.cart .button{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.emptyCart {
    margin-top: 120px;
    height: 40vh;
    width: 60vw;
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: #ebbbab;
    border-radius: 15px;
    position: relative;
    left:20%;    
}

.emptyCart>h1 {
    font-size: 3em;
    color: white;
}

.totalCart {
    text-align: right;
    width: 75%;
    color: black;
    font-size: 2.5em;
}

.cartControls {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 90%;
}

@media screen and (max-width:576px) {

    .cart {
        width: 90vw;
        margin:0;
        margin-top: 189px;
        left:5%;
        border-radius: 10px;        
    }    
    
    .emptyCart {       
        height: 22vh;
        width: 90vw;        
        left: 5%; 
        top:85px;
    }

    .emptyCart>h1 {
        font-size: 2.5em;
        padding:5px;
    }    
   }